.paper {
  height: 55vh;
  overflow-y: scroll;
}

.tablecard {
  display: inline-block;
  width: 40%;
}

.nglcard {
  display: inline-block;
  width: 60%;
  height: 55vh;
}

.margined {
  margin: 1em;
}

.container {
  height: 100%;
}

.selectedrow {
  background-color: #76ff03;
  white-space: pre-wrap;
}

.row {
  white-space: pre-wrap;
}

.graph {
  height: 40vh;
}

.graph-placeholder {
  position: absolute;
  width: 100%;
  height: 40vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  z-index: 9999;
}
